import { v4 } from "uuid"
import { ClerkUnsafeMetadata } from "@rp/react/web/common/hooks"
import { PasswordStepFormValues } from "./PasswordStep.types"

const getSignUpUpdateValues = (
  values: Omit<PasswordStepFormValues, "terms">,
  unsafeMetadata: ClerkUnsafeMetadata,
) => ({
  firstName: values.first,
  lastName: values.last,
  password: values.secret,
  legalAccepted: true,
  unsafeMetadata: {
    ...unsafeMetadata,
    initialLoginId: v4(),
  },
})

export { getSignUpUpdateValues }
